.page.debates {
  text-align: center; }
  .page.debates .bottom-container {
    justify-content: space-around;
    align-items: stretch; }
    .page.debates .bottom-container .topic-container {
      align-self: center; }
  .page.debates .topic-container {
    transform: translateX(100vw);
    animation-name: fly-in-right-with-shadow;
    animation-duration: 1s;
    animation-fill-mode: forwards; }
  .page.debates .circle-m,
  .page.debates .circle-s {
    opacity: 0;
    animation-delay: 1s;
    animation-name: fade-in-and-grow;
    animation-duration: 1s;
    animation-fill-mode: forwards; }
