.page.questions h1 {
  max-width: 75%;
  max-height: 25%;
  margin-top: 32px;
  font-size: 48px; }
  .page.questions h1 span:first-child {
    color: #1c4c6c; }

.page.questions .explore-score {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 112px;
  height: 112px; }

.page.questions .question-list-container {
  position: relative;
  display: flex;
  min-height: 0;
  flex-direction: column;
  flex: 0 1 auto;
  opacity: 0;
  animation-name: fade-in;
  animation-duration: 0.5s;
  animation-fill-mode: forwards; }

.page.questions .question-list li {
  position: relative;
  padding: 0px 0px 16px 0px;
  min-height: 72px;
  font-family: "Cheddar Gothic Sans";
  font-size: 24px;
  z-index: 1;
  line-height: 1;
  transform: translateY(100vh);
  animation-name: fly-in-bottom;
  animation-fill-mode: forwards;
  animation-duration: 2s;
  animation-delay: 0.5s; }
  .page.questions .question-list li:last-child {
    margin-bottom: 0; }
  .page.questions .question-list li:after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 60%;
    height: 10px;
    top: 1px;
    left: 50px;
    z-index: -10;
    box-shadow: 0px 2px 10px 5px #68a6d6;
    background-color: #fff;
    transition: transform 1s ease; }
  .page.questions .question-list li:hover:after {
    transform: scale(1.1); }
  .page.questions .question-list li span {
    display: inline-block;
    vertical-align: top;
    background-color: #fff; }
  .page.questions .question-list li .question-number {
    position: relative;
    width: 50px;
    height: 52px;
    color: #fff;
    background-color: #47ace3;
    text-align: center;
    padding-top: 16px;
    margin-top: -8px; }
    .page.questions .question-list li .question-number:after {
      content: '';
      position: absolute;
      z-index: -1;
      height: 0;
      width: 120%;
      left: -10%;
      top: 0;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-bottom: 8px solid #317072; }
  .page.questions .question-list li .question {
    width: calc(100% - 50px);
    padding-left: 8px;
    padding-right: 15vw;
    padding-top: 8px; }
  .page.questions .question-list li .circle {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 1px;
    height: 56px;
    width: 56px; }
    .page.questions .question-list li .circle p {
      margin: 0; }
    .page.questions .question-list li .circle p:nth-child(2) {
      font-size: 12px;
      text-transform: uppercase; }

.page.questions .tip {
  bottom: 16px;
  margin-top: 32px;
  padding: 32px;
  padding-bottom: 0;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1; }
