.page.reasons .top-container h1,
.page.reasons .top-container h2 {
  text-align: left; }

.page.reasons .top-container .position {
  display: inline-block;
  vertical-align: bottom;
  width: 33%;
  margin-right: 3%;
  margin-bottom: calc(32px + 8px);
  animation-name: fade-in;
  animation-duration: 1s;
  animation-fill-mode: forwards; }

.page.reasons .top-container .title-block {
  display: inline-block;
  width: 63%;
  vertical-align: bottom;
  transform: translateX(100vw);
  animation-name: fly-in-right;
  animation-duration: 1s;
  animation-fill-mode: forwards; }

.page.reasons .reasons-list {
  padding-bottom: 32px; }
  .page.reasons .reasons-list li {
    position: relative;
    margin-bottom: 16px;
    transform: translateY(100vh);
    animation-name: fly-in-bottom;
    animation-fill-mode: forwards;
    animation-duration: 1s;
    animation-delay: 1s; }
    .page.reasons .reasons-list li .rectangle {
      height: 100%; }
    .page.reasons .reasons-list li a {
      position: absolute;
      top: 3px;
      right: 3px;
      font-family: "Cheddar Gothic Sans";
      font-size: 12px; }
    .page.reasons .reasons-list li h3 {
      vertical-align: bottom; }
      .page.reasons .reasons-list li h3:first-line {
        color: #1c4c6c; }

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .page.reasons .top-container {
    padding: 0 125px; } }
