.position {
  position: relative;
  display: inline-block;
  padding: calc(55px - 16px) 8px 0 8px;
  margin-bottom: 64px;
  width: 25%; }
  .position .speech-bubble {
    width: 100%;
    text-align: center;
    padding: 16px; }
  .position .explore-score {
    position: absolute;
    z-index: 10;
    top: 0px;
    right: 48px; }
  .position .position-count {
    position: absolute;
    z-index: 20;
    top: 0px;
    right: 0px; }
  .position .position-count.circle-s.circle p {
    margin: 0; }
    .position .position-count.circle-s.circle p:first-child {
      font-size: 24px; }
    .position .position-count.circle-s.circle p:nth-child(2) {
      font-size: 12px; }

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .position .speech-bubble {
    width: 90%;
    margin-left: 5%; } }
