.topic-container {
  position: relative; }
  .topic-container a {
    text-decoration: none; }
  .topic-container h2:first-line {
    color: #1c4c6c; }
  .topic-container .topic-title {
    position: relative;
    left: 1%; }
  .topic-container .topic-search {
    position: absolute;
    left: 0;
    bottom: 0; }
  .topic-container .explore-score {
    position: absolute;
    left: 200px;
    bottom: -30px; }
  .topic-container .topic-media {
    position: absolute;
    left: 240px;
    bottom: 60px; }
  .topic-container .topic-subdebates {
    position: absolute;
    left: 220px;
    top: 0; }
  .topic-container .topic-help {
    position: absolute;
    top: -20px;
    left: 190px; }
